 :root{
      --Header-background: #fffae4;/* linear-gradient(90deg,#fffae4 calc(20% + 100px)/*,#fff3c2 70% ,#fffae4 ); */
     --Header-color: #633313;
     --Header-links-background: linear-gradient(90deg, #f0a24a 32%,#d78c35/* #964e1d*/);
     --Header-links-color: black;
 }


 .Header{
    text-align: center;
    color: var(--Header-color);
}
.Header-header{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--Header-background);
    border-bottom: solid 4px var(--Header-links-color);
    border-top: solid 4px var(--Header-links-color);
    margin: 0 0px;
    padding: 10px 20px;
    box-sizing: border-box;
    height: 120px;
    /* border-radius: 0px 0px 00px 10% 
    / 0px 0px 00px 0px; */

}
.Header-logo{
    width: auto;
    height: 100px;
    margin: 5px 10px;
    /* -webkit-transform: scaleX(-1);
    transform: scaleX(-1); */
    /* position: absolute; */
    /* right: 0px; */
    
}
.Header-description{
    padding: 0;
    margin: 0;
    text-align: right;
}
.Header-title{
    padding: 0;
    margin: 0;
    text-align: right;

 }
 :root{
     --Header-links-width:45px ;
 }
.Header-links{
    position: fixed;
    top:0;
    width: 100%;
    background: var(--Header-links-background);
    height: var(--Header-links-width);
    border-bottom: solid 4px var(--Header-links-color);
    white-space: pre;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center
}



.Header-links a{
    color: var(--Header-links-color);
    text-decoration: none;
    display: inline-block;
    /* height: 100%; */

    margin: 0px;
    padding: 10px 15px 10px 15px;

}
.Header-links a:hover{
    background-color: #f5deb344;
    /* background: linear-gradient(0deg, #f5deb300 1%,#f5deb377); */

}




.Header-link-down{
    position:relative;
}

html:has(.Header-link-down) {
    height: 100%;
}
.App:has(.Header-link-down) {
    margin-top: 0;
}

