.RightClick{
    position: absolute;
    right: -100px;
    bottom: -300px;
    background-color: #f1e6df90;
    width: 100px;
    height: 300px;
    -webkit-backdrop-filter:blur(15px);
    backdrop-filter: blur(15px);
    border-radius: 10px;
    overflow: hidden;
    direction: ltr;
    padding: 4px;
    box-sizing: border-box;
    box-shadow: 1px 1px 4px 0px;
    z-index: 9;
    display: flex;
    flex-direction: column;
    gap:3px;
}
.Letter{
    position: relative;
}
.RightClick>div{
    flex:1;
    border-radius: 7px;
    padding: 2px 15px;
    min-height: 0px;

}
.RightClick>div:hover{
    background: #7f7f7f55;
}
.RightClick>div:active{
    background: #7f7f7f;
}

.RightClick-currentChord{
    border-bottom: solid;
    background: #7f7f7f55;
}
.RightClick>div.RightClick-currentChord:hover{
    background: #7f7f7f;
}

.RightClick-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: auto;
    /* pointer-events: none; */
    z-index: 8;
        
    /* background: #110303;
    opacity: .5; */
   

}