.Piano {
    position: absolute;
    /*/transform: scale(.6);*/
    right: 10px;
    direction: ltr;
    cursor: pointer;
    display: flex;
    top: 2px;
    right: 2px;
    width:calc(100% - 4px);
    height: calc(100% - 4px); 
}
.Piano-box{
    position: relative;
    overflow: hidden;
    width: 100%;
    padding-top: 55%;
}
.Piano i{
    font-style: normal;
    margin-bottom: 5px;
}
.Piano :is(.C,.D,.E,.F,.G,.A,.B){
    flex:1;

    position: relative;
    background-color: white;
    outline: #000000 solid 2px;
    border-radius: 0 0 20% 20% / 0 0 6% 6%;
    display: grid;
    grid-template-rows: 1.5fr 1fr;
    justify-items: center;
    align-items: end;
    box-sizing: border-box;

}
.Piano :is(.Db,.Eb,.Gb,.Ab,.Bb){
    position: relative;
    z-index: 1000;
    width: 75%;
    height: 100%;
    right: -50%;
    background-color: black;
    border-radius: 0 0 30% 30% / 0 0 9% 9%;
    color: white;
    direction: ltr;
    display: grid;
    justify-items: center;
    align-items: end;    
}
.Piano .current{
    background-color: #ccc;
    color: black;
}
.Piano .C{ z-index: 7; border-top-left-radius: 20% 6%;}
.Piano .D{ z-index: 6; }    
.Piano .E{ z-index: 5; }    
.Piano .F{ z-index: 4; }    
.Piano .G{ z-index: 3; }    
.Piano .A{ z-index: 2; }    
.Piano .B{ z-index: 1; border-top-right-radius: 20% 6%;} 
