.Letter{
    display: inline-flex;
    flex-direction: column-reverse;
    cursor: pointer;
}

.Letter .chord{
    position: absolute;
} 
.line{
    display: flex;
    margin: 15px 0;
}
:is(
    .Letter:has(:is(.Letter-letter:hover,.chord:hover)),
    .Letter-focus
) :is(.chord, .Letter-letter)
{
    background: #00000044;
 }
 

dialog{
    display: block;
    position: fixed;
}