.Left{
    position:fixed ;
    top: 0px;
    left: -300px;
    height: 100%;
    width: 300px;
    background: var(--app-background);
    /*opacity: .7;*/
    box-sizing: border-box;
    visibility: hidden;
    transition:left 1.5s, visibility 0s 1.5s;
    z-index: 92;
    overflow-x: hidden;
    overflow-y: auto;
    box-shadow: 4px 0px 0px 0px black;
    text-align: center;

}

.Left-header{
    display: block;
    height: var(--Header-links-width);
    box-shadow: 0px 0px 1px 0px black;
    background: var(--Header-links-background);
}

body:has(.Left-checkbox:not(:checked))
.Left-header{
    pointer-events: none;
}

.Left-main{
    margin: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: calc(100% - 100px);
    direction: rtl;
}

.Left{
    direction: ltr;

}



.Left-textarea {
    flex:1;
    resize: none;
    /* height: 100%; */
    /* height: 200px; */
}