.Sulam{
    direction:ltr;
    text-align: right;
}
.Sulam>div{

display: grid;
grid-template-columns: 1fr 1fr 1fr 1fr;
justify-content: center;
}
.Sulam-choose, .Sulam-choose-m{
    background: white;
    padding: 5px;
    margin: 10px 2px;
    box-shadow: 1px 1px 0px 0px black;/**/
    
}

.Sulam-chord{
    background: #ffffff;
    border: solid 1px black;
    /* border-radius: 2px; */
    padding: 10px;
}

.Sulam-chord:hover{
    background: #eee;
}

.Sulam-chord.Sulam-currentChord
{
    background: #ccc;
}