:root{
  --app-background: #fffbed;
  --app-shadow: 0px 0px 0px 0px black;
  --app-border: solid 1px black;

}

html{
  height: calc(100% - var(--Header-links-width));
}
body,#root{
  height: 100%;
  background-color: #f1e6df;

}
.App{
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: var(--Header-links-width);
  
}

.Main{
  flex:1;
  display: flex;
  flex-direction: row-reverse;
  margin: 0 0  0 20px;
  gap: 20px; 

}
.Main-main{
  flex:3;
  background-color: var(--app-background);
  box-shadow: var(--app-shadow);
  padding: 30px;
  /* border: var(--app-border); */
}
.Right{
  /* background-color: var(--app-background); */
  box-shadow: var(--app-shadow);
  box-sizing: border-box;
  width: 250px;
  border: var(--app-border);

}
.Footer{
  background-color: var(--app-background);
  box-shadow: var(--app-shadow);
  border-top: var(--app-border);
}

