@media screen and (max-width:570px) {
  .Main{
    flex-direction:column-reverse;
  }
  .Main .Right{
    width: 100%;
    display: flex;
    align-items: stretch;
  }
  .Main .Right-chord{
    margin: 10px 15%;
    width: 70%;
    
  }
  .Piano{
    font-size: small;
  }
  .Main{
    margin: 0;
  }
}


@media  print{
  #root :is(.Header,.Right, .Left,.Left-click, .Footer){
    display: none;
  }
  .Main{
    margin: 0;
  }
  .Main-main{
    background: #fff;
  }

}



/*body:has(#Left-stk:checked)*/
@media screen and (min-width:570px) {
body:has(#Left-stk:checked)
 #root .Left{
  position: relative;
  visibility: visible;
  top:0px!important;
  left: 0;
  z-index: 0;
}
body:has(#Left-stk:checked)
#root .Left-header{
  display: none;
}

body:has(#Left-stk:checked)
#root .Left-click-bg{
  pointer-events:none ;
}
body:has(#Left-stk:checked)
.App{
  display: grid;
  grid-template-areas:
  'Header  Header'
   'Main Left_'
  'Footer Footer';
  grid-template-columns: 1fr 300px;
  gap: 0;

}
body:has(#Left-stk:checked)
.Main{margin: 20px 0 20px 20px;}

.Main{grid-area: Main;}
.Header{grid-area: Header;}
.Left{grid-area: Left_;}
.Footer{grid-area: Footer;}
}