
.RightButton{
    padding: 20px 0;
}
.RightButton-button{
    background: #ffffff;
    border: solid 1px black;
    border-radius: 2px;
    padding: 10px;
    margin: 2px;
}

.RightButton-button:hover{
    background: #eee;
}

.RightButton-button.current,
.RightButton-button:active{
    background: #ccc;
}
