:root{
 --Left-click-checked-background:#ffffff55;
 --Left-click-color:#000000;

}

.Left-click{
    position: fixed;
    display: block;
    top:0px;
    left: 0px;
    width: var(--Header-links-width);
    height: var(--Header-links-width);
    transition: background-color 2s ;
    z-index: 93;
    /* background: #21084d53; */
    border-right: solid 2px black;
    box-shadow: 2px 0 0 0 black;
    box-sizing: border-box;

    /* border: solid 3px #110303; */
}
body:has(.Left-checkbox:checked) 
.Left-click{
    background: var(--Left-click-checked-background);
}

.Left-checkbox{
    position: fixed;
    pointer-events: none;
    opacity: 0;
    z-index: -1;
}
.Left-click > div{
    position: absolute;
    top: 10px;
    Left: 7px; 
    transform: scale(1);
}

.Left-click > div > div {
    border:solid 2px var(--Left-click-color);
    border-radius: 30%;
    width: 20px;
    height: 0px;
    margin: 4px;
    padding: 0;
    opacity:1;
    transition:transform 1s;

}


body:has(.Left-checkbox:checked)
.Left-click > div > div:nth-child(2){
    transform: rotate(-45deg) scale(1.2);

}
body:has(.Left-checkbox:checked)
.Left-click > div > div:nth-child(1){
    transform:  rotate(45deg)  translate(5px ,5px) scale(1.2);

}
body:has(.Left-checkbox:checked)
.Left-click > div > div:nth-child(3){
    opacity: 0;
    transform:  rotate(45deg)  translate(-5px ,-5px) scale(1.2);
    transition:transform 1s, opacity 0.5s 1s;

}
body:has(.Left-checkbox:checked) 
.Left {
    visibility: visible;
    left: 0;
    transition:left 1.5s;

}

.Left-click-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: auto;
    pointer-events: none;
    z-index: 91;
    /*     
    background: #110303;
    opacity: .5;
    */

}

body:has(.Left-checkbox:checked) 
.Left-click-bg{
    pointer-events: auto;
}
