.Right{
    position: relative;
    display: flex;
    flex-direction: column;
}

.Right-checkboxs{
    position: absolute;
    pointer-events: none;
    opacity: 0;
}
.Right-checkboxs-label{
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-bottom: solid 1px black;
    border-top:  solid 1px black;
    box-sizing: border-box;
    background: white;
}
.Right-checkboxs-label:hover{
    background: #eee;
}
.Right-box1{
    overflow: hidden;
    max-height:500px;
    transition:max-height 1s linear;

}
.Right-box2{
    overflow: hidden;
    max-height:500px;
    transition:max-height 1s linear;

}

div:has(>.RightButton){
    transition:max-height 2s;

}
body:has(#Right-checkbox1:checked) 
.Right-box1{
    max-height:00px
}


body:has(#Right-checkbox2:checked)
.Right-box2{    
    max-height: 0;
}

.RightButton ,.Sulam{
    margin-bottom: 20px;
}
.RightChord{
    background: var(--app-background);
}
